@import url('https://fonts.googleapis.com/css2?family=Bodoni+Moda:opsz,wght@6..96,400;6..96,700&display=swap');
body {
    font-family: sans-serif;
    padding-top: 3.5rem;
    padding-left: 10rem;
    padding-right: 10rem;
    margin: 0;

    @media screen and (max-width: 900px) {
        padding: 3rem;
    }
}

a {
    color: black;
    text-decoration: none;
    font-weight: 600;
}

a:hover {
    color: hotpink;
    text-decoration: none;
}

.current {
    color: hotpink;
}

nav a {
    text-align: center;
}

.navigation {
    align-items: center;
}

h1 {
    font-family: 'Bodoni Moda', serif;
    font-size: 2.25rem;
    font-weight: 300;
    text-decoration: underline;
    text-decoration-thickness: 2px;
}


h2 {
    font-family: 'Bodoni Moda', serif;
    font-size: 3rem;
    font-weight: 300;
}

// .row {
//     width: 100%;
// }

.headshot {
    align-items: center;
    justify-content: center;
    display: flex;
}

.circle {
    border-radius: 50%;
    max-width: 50%;
    @media screen and (max-width: 750px) {
        max-width: 80%;
    }
}

.skills {
    margin-top: 3rem;
    font-weight: 600;
    line-height: 2rem;
}

.experience {
    margin-top: 3rem;
}

.contained {
    padding-top: 5%;
    // padding-left: 10%;
    // padding-right: 10%;
    align-content: center;
}

/*Footer*/

.tiny {
    max-width: 10%;
}

.no-bullet {
    list-style-type: none;
}

footer {
    padding-top: 5em;
    padding-bottom: 2em;
    color: black;
    font-weight: 600;
    font-size: .75rem;
}

/* Resume */

.resume-image {
    max-width: 90%;
}